@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-BoldItalic.woff2") format("woff2"), url("../fonts/Montserrat-BoldItalic.woff") format("woff");
  font-weight: 700;
  font-style: italic;
  font-display: swap; }

input[type="radio"].visually-hidden,
input[type="checkbox"].visually-hidden,
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  width: 100%;
  min-width: 1200px;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 160%;
  font-weight: 400;
  color: #ffffff;
  background-color: #7b5449; }

img {
  max-width: 100%;
  height: auto; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding-left: 48px;
  padding-right: 32px; }

.opacity-none {
  opacity: 0; }

.display-none {
  display: none; }

.button {
  display: inline-block;
  box-sizing: border-box;
  min-width: 100px;
  margin: 0;
  padding: 23px 31px 24px 30px;
  appearance: none;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 15px;
  line-height: 18px;
  font-weight: 700;
  letter-spacing: 0.03em;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent;
  border: 1px solid #ffffff;
  cursor: pointer; }

.button:focus,
.button:hover {
  background-color: rgba(255, 255, 255, 0.1); }

.button:active {
  background-color: rgba(255, 255, 255, 0.3); }

.button span {
  display: flex;
  align-items: center; }

.button span::after {
  content: "";
  width: 32px;
  height: 9px;
  background-image: url("../img/icon-arrow.svg");
  background-repeat: no-repeat;
  background-position-x: 11px; }

.main-header {
  position: relative;
  width: 100%;
  z-index: 2;
  background-color: transparent; }

.main-footer {
  margin-top: auto;
  background-color: #1d1d1d;
  width: 100%; }

.main-footer__wrapper {
  display: flex;
  align-items: center; }

.main-footer__links {
  display: flex;
  margin-left: auto;
  list-style: none;
  padding: 0; }

.main-nav {
  position: absolute;
  left: 0;
  right: 0;
  display: flex;
  align-items: center; }

.main-nav__wrapper {
  display: flex;
  align-items: center; }

.main-nav__phone {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  text-decoration: none;
  color: #ffffff; }

.main-nav__logo {
  background-color: #ffffff; }

.site-navigation {
  padding: 0 0 10px 0;
  margin: 0;
  margin-left: auto;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  max-width: 72%; }

.site-navigation__item {
  margin-left: 20px; }

.site-navigation__link {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff; }

.slider-section {
  position: relative;
  height: calc(100vh - 54px);
  min-height: 846px;
  overflow: hidden; }

.slide-list {
  list-style: none;
  margin: 0;
  padding: 0; }

.slide-list__item {
  position: relative;
  min-width: 100%;
  height: 100%;
  z-index: 1;
  transition: opacity 0.3s ease-in-out; }

.slide-list__img {
  position: absolute;
  min-width: 100%;
  min-height: 100%;
  top: 0;
  left: 0;
  z-index: -1; }

.slide-list__img::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-image: linear-gradient(218.77deg, #703c26 13.28%, #424c53 85.15%);
  opacity: 0.85; }

.slide-list__img img {
  min-width: 100%;
  min-height: calc(100vh - 54px);
  object-fit: cover; }

.slide-list__content {
  z-index: 1;
  padding-top: 200px; }

.slide-list__controls {
  position: absolute;
  bottom: 150px;
  max-width: 1440px;
  margin: 0 auto;
  height: 54px;
  left: 30px;
  justify-content: flex-start;
  z-index: 2; }

.slide-list__slide-btn {
  width: 50px;
  height: 20px;
  background-color: #ffffff;
  margin-right: 10px;
  margin-bottom: 10px;
  padding: 0;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  transition: box-shadow 0.3s ease-in-out; }

.slide-list__slide-btn:hover,
.slide-list__slide-btn:focus {
  box-shadow: 0 0 1px 1px #ffffff; }

.slide-list h2 {
  width: 75%;
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 64px;
  line-height: 125%;
  font-weight: 700;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent; }

.slide-list p {
  width: 40%;
  line-height: 170%;
  font-weight: 500;
  margin: 0;
  margin-top: 38px; }

.slide-list b {
  margin-bottom: -19px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 80px;
  line-height: 100%;
  font-weight: 700;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent; }

.slide-list__details {
  display: flex;
  list-style: none;
  margin: 0;
  margin-top: 48px;
  margin-right: 30px;
  padding: 0; }

.slide-list__details-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-end; }

.slide-list__details li {
  display: flex;
  flex-direction: column;
  min-width: 160px;
  margin-right: 64px; }

.slide-list__details p {
  width: 100%;
  margin: 0;
  padding-top: 9px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent; }

.slide-list__details span {
  padding: 7px 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  text-transform: uppercase;
  color: #e75333;
  background-color: #ffffff;
  text-align: center; }

.slide-list__details-button {
  margin-bottom: 28px; }

@media (min-width: 1440px) {
  .slide-list__controls {
    left: 50%;
    transform: translateX(-700px); } }
